.schoolGradesPageContent{display:flex;width:100%;max-width:1440px;flex-direction:column;margin:0 auto}.schoolGradesPageContent .container{gap:20px}.schoolGradesPageContent .container .grades{width:100%;height:100%;display:flex;gap:50px}.schoolGradesPageContent .container .grades .grade{width:100%;flex:1;height:100%;border:1px solid rgba(0,0,0,.25);border-radius:10px;overflow:hidden;transition:all .3s}.schoolGradesPageContent .container .grades .grade .link{display:flex;align-items:center;justify-content:center;width:100%;height:150px;text-decoration:none;color:#fff;font-family:Roboto;font-size:40px;text-transform:uppercase;transition:all .3s}.schoolGradesPageContent .container .grades .grade .link:hover{transform:scale(1.25)}.schoolGradesPageContent .container .grades .ce2{background-color:rgba(143,25,45,.6)}.schoolGradesPageContent .container .grades .ce2:hover{background-color:rgba(143,25,45,.8)}.schoolGradesPageContent .container .grades .ce2 .link{text-shadow:2px 3px 1px #8f192d}.schoolGradesPageContent .container .grades .cm1{background-color:rgba(28,42,133,.6)}.schoolGradesPageContent .container .grades .cm1:hover{background-color:rgba(28,42,133,.8)}.schoolGradesPageContent .container .grades .cm1 .link{text-shadow:2px 3px 1px #1c2a85}.schoolGradesPageContent .container .grades .cm2{background-color:rgba(1,97,95,.6)}.schoolGradesPageContent .container .grades .cm2:hover{background-color:rgba(1,97,95,.8)}.schoolGradesPageContent .container .grades .cm2 .link{text-shadow:2px 3px 1px #01615f}